
import React,{useState} from 'react'
import {crearCategoria} from '../Services/categoriaServices'

export default function FormCategoria(){

  const [nombreCategoria, setNombreCategoria]=useState("")
  //esta funcion recibe el evento del formuario
  const manejarSumbit=async (ev)=>{
    //pevenimos que se ejecute el evento predeterminado del submit
    ev.preventDefault()
    //creamos el objeto tal como le pide el backend
    let objCategoria = {
      nombre:nombreCategoria
    }
    //hago la petición
    let response = await crearCategoria(objCategoria)
    console.log(response)
  }

  return(
    <div>
      <form onSubmit={(ev) => manejarSumbit(ev) }>
        <div className="mb-2">
          <label className="form-label">Nombre de la Categoría</label>
          <input className="form-control" type="text" value={nombreCategoria}
          onChange={(e)=>{setNombreCategoria(e.target.value)}} />
          <small className="form-text">Ej.: Ofertas ó Regalos</small>
        </div>
        <button type="submit" className="btn btn-primary">Crear Categoría</button>
      </form>      
    </div>
  )
}